@import url(https://fonts.googleapis.com/css2?family=Georama:ital,wght@0,100;0,300;0,400;1,400&display=swap);
body {
  margin: 0; padding: 0px; color: white;
  height: 100vh; width: 100vw;
  font-family: 'Georama', sans-serif;

  background-image: linear-gradient(
  33deg, 
  rgb(34, 9, 73) 35%, 
  rgb(58, 37, 82),
  rgb(58, 37, 82),
  rgb(58, 37, 82) );
}

#divMain {
  height: 100vh; width:100vw;
  display: flex; align-items: center;
  justify-content: center;
  text-align: center;
}

table {
  border-collapse: collapse;
  border-radius: 2rem;
  width: 300px;
  height: 300px;
  align-items: center;
  
  background-image: linear-gradient(
  to top, 
  rgb(34, 9, 73), 
  rgb(58, 37, 82),
  rgb(58, 37, 82),
  rgb(58, 37, 82) );
}

td {
  border: solid 4px white;
}

td:hover{
  background-color:   rgb(64, 22, 131);
}

td:active{
  /* animation: f 1s normal 0s normal; */
  animation: f .2s normal;
}

button{
  border: solid 1px white;
  border-radius: 5px;
  background-color: rgb(56, 107, 228);
  color: white;
  width: 75px; height: 40px;
}

button:hover{
  background-color: rgb(35, 91, 221);
}

@keyframes f {
  0%{
    border-width: 4px;
  }

  25%{
    border-width: 5px;
    border-color: thistle;
  }

  50%{
    border-width: 7px;
    border-color: thistle;
  }

  75%{
    border-width: 5px;
    border-color: thistle;
  }

  100%{
    border-width: 4px;
  }

}



